import { printCreditConstants } from "./constants";
// actions
export const loadingAction = () => ({
  type: printCreditConstants.LOADING_PRINT_CREDIT,
});
export const loadingUpdatedAction = () => ({
  type: printCreditConstants.LOADING_UPDATED,
});
export const getPrintSuccessAction = (data) => ({
  type: printCreditConstants.GET_PRINT_CREDIT_SUCCESS,
  payload: data,
});
export const getPrintFailAction = (error) => ({
  type: printCreditConstants.GET_PRINT_CREDIT_FAIL,
  payload: error,
});

export const createPrintSuccessAction = (data) => ({
  type: printCreditConstants.CREATE_PRINT_CREDIT_SUCCESS,
  payload: data,
});

export const createPrintFailAction = (error) => ({
  type: printCreditConstants.CREATE_PRINT_CREDIT_FAIL,
  payload: error,
});

export const deletePrintSuccessAction = (data) => ({
  type: printCreditConstants.PRINT_CREDIT_DELETED_SUCCESS,
  payload: data,
});

export const deletePrintFailAction = (error) => ({
  type: printCreditConstants.PRINT_CREDIT_DELETED_FAIL,
  payload: error,
});

export const updatedPrintSuccessAction = (data) => ({
  type: printCreditConstants.PRINT_CREDIT_UPDATE_SUCCESS,
  payload: data,
});

export const updatedPrintFailAction = (error) => ({
  type: printCreditConstants.PRINT_CREDIT_UPDATE_FAIL,
  payload: error,
});
