import axiosInstance from "../../utils/axios";

//obtaining the paginated data
export const getPrint = (postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/print-credit?offset=0&limit=${postsPerPage}&ordering=-id`
  );

//obtaining all data
export const getAllPrint = () =>
  axiosInstance.get(`api/v1/core-app/print-credit?ordering=-id`);

//obtaining the previous page data from paginated data
export const getPrevious = (previous) => axiosInstance.get(previous);

//obtaining the next page data from paginated data
export const getNext = (next) => axiosInstance.get(next);

//obtaining the particular page data from paginated data
export const getParticularPage = (number, postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/print-credit?offset=${
      (number - 1) * postsPerPage
    }&limit=${postsPerPage}&ordering=-id`
  );

//creating function
export const createPrint = (body) =>
  axiosInstance.post(`api/v1/core-app/print-credit`, body, {
    "Content-Type": "multipart/form-data",
  });

//deleting function
export const deletePrint = (id) =>
  axiosInstance.delete(`api/v1/core-app/print-credit/${id}`);

//update function
export const updatePrint = (id, body) =>
  axiosInstance.patch(`api/v1/core-app/print-credit/${id}`, body, {
    "Content-Type": "multipart/form-data",
  });

//searching function
export const handleSearch = (search, postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/print-credit?offset=0&limit=${postsPerPage}&search=${search}`
  );
