import React, { useState, useRef, useEffect } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import TextError from "../../Component/TextError/TextError";
import { useSelector, useDispatch } from "react-redux";
import {
  createPrintCredit,
  updatePrintCredit,
} from "../../Redux/PrintCredit/thunk";
import Button from "../../Component/Button/Button";
import { useHistory } from "react-router-dom";
import { AsyncPaginate } from "react-select-async-paginate";
import axiosInstance from "../../utils/axios";
import Thumb from "../../Component/Thumb";
import preeti from "preeti";
import { errorFunction } from "../../Component/Alert";
const CreatePrintCredit = ({ currentPage }) => {
  // props
  const history = useHistory();
  const print = useSelector((state) => state.print.print);
  const loading = useSelector((state) => state.print.loading);
  const edit = useSelector((state) => state.print.edit);
  const dispatch = useDispatch();
  //state for disabling the submit button
  const [lock, setLock] = useState(false);
  const [qr, setQR] = useState(null);
  const [ownerPhoto, setOwnerPhoto] = useState(null);
  //initial state of the form
  const initialState = {
    first_name: edit ? print?.first_name : "",
    first_name_nepali: edit ? print?.first_name_nepali : "",
    middle_name: edit ? print?.middle_name : "",
    middle_name_nepali: edit ? print?.middle_name_nepali : "",
    last_name: edit ? print?.last_name : "",
    last_name_nepali: edit ? print?.last_name_nepali : "",
    address: edit ? print?.address : "",
    address_nepali: edit ? print?.address_nepali : "",
    citizenship_no: edit ? print?.citizenship_no : "",
    citizenship_no_nepali: edit ? print?.citizenship_no_nepali : "",
    mobile_no: edit ? print?.mobile_no : "",
    mobile_no_nepali: edit ? print?.mobile_no_nepali : "",
    district: edit ? print?.district : null,
    branch: edit ? print?.branch : null,
    qr: "",
    owner_photo: "",
    remarks: "",
  };

  //validation rules of the form
  const validationSchema = Yup.object().shape({
    first_name: Yup.string()
      .required("First Name is required.")
      .min(1, "First Name must be at least 1 characters.")
      .max(50, "First Name must be at least 50 characters."),
    first_name_nepali: Yup.string()
      .required("First Name is required.")
      .min(1, "First Name must be at least 1 characters.")
      .max(50, "First Name must be at least 50 characters."),
    middle_name: Yup.string().max(50, "Name must be at least 50 characters."),
    middle_name_nepali: Yup.string().max(
      50,
      "Name must be at least 50 characters."
    ),
    last_name: Yup.string()
      .required("Last Name is required.")
      .min(1, "Last Name must be at least 1 characters.")
      .max(50, "Last Name must be at least 50 characters."),
    last_name_nepali: Yup.string()
      .required("Last Name is required.")
      .min(1, "Last Name must be at least 1 characters.")
      .max(50, "Last Name must be at least 50 characters."),
    mobile_no: Yup.string()
      .required("Mobile Number is required")
      .max(10, "Mobile Number can be of 10 characters only"),
    mobile_no_nepali: Yup.string()
      .required("Mobile Number is required")
      .max(10, "Mobile Number can be of 10 characters only"),
    citizenship_no: Yup.string()
      .required("CitizenShip Number is required")
      .max(20, "CitizenShip Number can be of 10 characters only"),
    citizenship_no_nepali: Yup.string()
      .required("CitizenShip Number is required")
      .max(20, "CitizenShip Number can be of 10 characters only"),
    address: Yup.string()
      .required("Address is required")
      .max(50, "Address can be of 50 characters only"),
    address_nepali: Yup.string()
      .required("Address is required")
      .max(50, "Address can be of 50 characters only"),
    district: Yup.object().typeError("District is required").nullable(false),
    branch: Yup.object().typeError("Branch is required").nullable(false),
    qr: !edit ? Yup.mixed().required("QR is required") : Yup.mixed(),
    owner_photo: !edit
      ? Yup.mixed().required("Owner photo is required")
      : Yup.mixed(),
    remarks:
      edit &&
      Yup.string()
        .required("Remarks is required.")
        .min(1, "Remarks Name must be at least 1 characters."),
  });
  const onSubmit = (values) => {
    const { district, branch, remarks } = values;

    setLock(true);
    if (edit) {
      const id = print?.id;
      dispatch(
        updatePrintCredit(id, history, {
          ...values,
          district: district?.id,
          branch: branch?.id,
          remarks,
        })
      );
    } else {
      dispatch(
        createPrintCredit(
          {
            ...values,
            district: district?.id,
            branch: branch?.id,
          },
          history,
          currentPage
        )
      );
    }
    setLock(false);
  };
  const loadOptionsDistrict = async (
    search,
    loadOptions,
    { limit, offset }
  ) => {
    const { data } = await axiosInstance(
      `/api/v1/core-app/district-list?search=${search}&offset=${offset}&limit=${limit}`
    );

    return {
      options: data.results,
      hasMore: data.count > limit ? true : false,
      additional: {
        offset: data.count > limit ? limit + 1 : limit,
        limit: limit + 10,
      },
    };
  };
  const loadOptionsBranch = async (search, loadOptions, { limit, offset }) => {
    const { data } = await axiosInstance(
      `/api/v1/core-app/branch-list?search=${search}&offset=${offset}&limit=${limit}`
    );

    return {
      options: data.results,
      hasMore: data.count > limit ? true : false,
      additional: {
        offset: data.count > limit ? limit + 1 : limit,
        limit: limit + 10,
      },
    };
  };

  const handleChange = (e, formik) => {
    if (e.target.name === "first_name") {
      formik.setFieldValue("first_name", e.target.value);
      formik.setFieldValue("first_name_nepali", preeti(e.target.value));
    } else if (e.target.name === "middle_name") {
      formik.setFieldValue("middle_name", e.target.value);
      formik.setFieldValue("middle_name_nepali", preeti(e.target.value));
    } else if (e.target.name === "last_name") {
      formik.setFieldValue("last_name", e.target.value);
      formik.setFieldValue("last_name_nepali", preeti(e.target.value));
    } else if (e.target.name === "address") {
      formik.setFieldValue("address", e.target.value);
      formik.setFieldValue("address_nepali", preeti(e.target.value));
    } else if (e.target.name === "citizenship_no") {
      formik.setFieldValue("citizenship_no", e.target.value);
      formik.setFieldValue("citizenship_no_nepali", preeti(e.target.value));
    } else if (e.target.name === "mobile_no") {
      formik.setFieldValue("mobile_no", e.target.value);
      formik.setFieldValue("mobile_no_nepali", preeti(e.target.value));
    }
  };
  const handleCopy = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot copy the text.");
  };
  const handlePaste = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot Paste the text.");
  };
  const handleCut = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot cut the text.");
  };

  return (
    <>
      <div className="row  mt-2">
        <div className="col-12 ">
          <div className="registration-process ">
            <Formik
              enableReinitialize={true}
              initialValues={initialState}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
            >
              {(formik) => {
                return (
                  <Form autoComplete="off">
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-3">
                        <label
                          htmlFor="first_name"
                          className="fw-bold float-start"
                        >
                          First Name
                          <strong className="text-danger ">*</strong>
                          {formik.values.first_name_nepali}
                        </label>
                        <Field
                          value={formik.values.first_name}
                          type="text"
                          name="first_name"
                          className="form-control "
                          placeholder="First Name"
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                        />
                        <ErrorMessage name="first_name" component={TextError} />
                      </div>
                      <div className="col-3">
                        <label
                          htmlFor="middle_name"
                          className="fw-bold float-start"
                        >
                          Middle Name
                          {formik.values.middle_name_nepali}
                        </label>
                        <Field
                          type="text"
                          name="middle_name"
                          value={formik.values.middle_name}
                          className="form-control "
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          placeholder="Middle Name"
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                        />

                        <ErrorMessage
                          name="middle_name"
                          component={TextError}
                        />
                      </div>
                      <div className="col-3">
                        <label
                          htmlFor="last_name"
                          className="fw-bold float-start"
                        >
                          Last Name
                          <strong className="text-danger ">*</strong>
                          {formik.values.last_name_nepali}
                        </label>

                        <Field
                          type="text"
                          value={formik.values.last_name}
                          name="last_name"
                          className="form-control "
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          placeholder="Last Name"
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                        />
                        <ErrorMessage name="last_name" component={TextError} />
                      </div>
                      <div className="col-3">
                        <label
                          htmlFor="address"
                          className="fw-bold float-start"
                        >
                          Address
                          <strong className="text-danger ">*</strong>
                          {formik.values.address_nepali}
                        </label>
                        <Field
                          type="text"
                          name="address"
                          value={formik.values.address}
                          className="form-control "
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          placeholder="Address"
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                        />

                        <ErrorMessage name="address" component={TextError} />
                      </div>
                    </div>

                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-3">
                        <label
                          htmlFor="citizenship_no"
                          className="fw-bold float-start"
                        >
                          Citizenship Number
                          <strong className="text-danger ">*</strong>
                          {formik.values.citizenship_no_nepali}
                        </label>
                        <Field
                          type="text"
                          name="citizenship_no"
                          values={formik.values.citizenship_no}
                          className="form-control "
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          placeholder="Citizenship Number"
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                        />

                        <ErrorMessage
                          name="citizenship_no"
                          component={TextError}
                        />
                      </div>
                      <div className="col-3">
                        <label
                          htmlFor="mobile_no"
                          className="fw-bold float-start"
                        >
                          Mobile Number
                          <strong className="text-danger ">*</strong>
                          {formik.values.mobile_no_nepali}
                        </label>
                        <Field
                          type="text"
                          value={formik.values.mobile_no}
                          name="mobile_no"
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          onChange={(e) => {
                            handleChange(e, formik);
                          }}
                          className="form-control "
                          placeholder="Mobile Number"
                        />

                        <ErrorMessage name="mobile_no" component={TextError} />
                      </div>
                      <div className="col-3">
                        <label
                          htmlFor="district"
                          className="fw-bold float-start"
                        >
                          District
                          <strong className="text-danger ">*</strong>
                        </label>
                        <AsyncPaginate
                          value={formik.values.district}
                          isClearable="true"
                          isSearchable="true"
                          name="district"
                          inputId="input"
                          getOptionLabel={(option) => `${option.name_nepali} `}
                          getOptionValue={(option) => `${option?.id}`}
                          onChange={(selected) => {
                            formik.setFieldValue("district", selected);
                          }}
                          loadOptions={loadOptionsDistrict}
                          additional={{
                            offset: 0,
                            limit: 10,
                          }}
                        />
                        <ErrorMessage name="district" component={TextError} />
                      </div>
                      <div className="col-3">
                        <label htmlFor="branch" className="fw-bold float-start">
                          Branch
                          <strong className="text-danger ">*</strong>
                        </label>
                        <AsyncPaginate
                          value={formik.values.branch}
                          isClearable="true"
                          isSearchable="true"
                          name="branch"
                          inputId="input"
                          getOptionLabel={(option) => `${option.name} `}
                          getOptionValue={(option) => `${option?.id}`}
                          onChange={(selected) => {
                            formik.setFieldValue("branch", selected);
                          }}
                          loadOptions={loadOptionsBranch}
                          additional={{
                            offset: 0,
                            limit: 10,
                          }}
                        />
                        <ErrorMessage name="branch" component={TextError} />
                      </div>
                    </div>
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-4">
                        <label htmlFor="qr" className="fw-bold float-start">
                          Upload QR(32*32)
                          <strong className="text-danger ">*</strong>
                        </label>
                        <input
                          type="file"
                          name="qr"
                          className="form-control form-control-sm"
                          onChange={(event) => {
                            formik.setFieldValue("qr", event.target.files[0]);
                            let reader = new FileReader();
                            reader.readAsDataURL(event.target.files[0]);
                            reader.onloadend = () => setQR([reader.result]);
                          }}
                        />
                        {qr && <Thumb thumb={qr} />}
                        {print && print?.qr && !qr && (
                          <Thumb thumb={print?.qr} />
                        )}
                        <ErrorMessage name="qr" component={TextError} />
                      </div>
                      <div className="col-4">
                        <label
                          htmlFor="owner_photo"
                          className="fw-bold float-start"
                        >
                          Upload Photo(32*32)
                          <strong className="text-danger ">*</strong>
                        </label>
                        <input
                          type="file"
                          name="owner_photo"
                          className="form-control form-control-sm"
                          onChange={(event) => {
                            formik.setFieldValue(
                              "owner_photo",
                              event.target.files[0]
                            );
                            let reader = new FileReader();
                            reader.readAsDataURL(event.target.files[0]);
                            reader.onloadend = () =>
                              setOwnerPhoto([reader.result]);
                          }}
                        />
                        {ownerPhoto && <Thumb thumb={ownerPhoto} />}
                        {print && print?.owner_photo && !ownerPhoto && (
                          <Thumb thumb={print?.owner_photo} />
                        )}
                        <ErrorMessage
                          name="owner_photo"
                          component={TextError}
                        />
                      </div>
                    </div>
                    <div className="form-group row my-2 justify-content-center">
                      {edit && (
                        <div className="col-3">
                          <label htmlFor="remarks" className="fw-bold">
                            Remarks
                            <span className="text-danger">*</span>
                          </label>
                          <textarea
                            type="text"
                            name="remarks"
                            onCopy={handleCopy}
                            onCut={handleCut}
                            onPaste={handlePaste}
                            onChange={(e) => {
                              formik.setFieldValue("remarks", e.target.value);
                            }}
                            className="form-control"
                            placeholder="Enter remarks"
                          />
                          <ErrorMessage name="remarks" component={TextError} />
                        </div>
                      )}
                    </div>

                    <div className="form-group text-center">
                      {!edit ? (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary"}
                          loading={loading}
                          disabled={lock || !formik.dirty}
                          title={"Create"}
                          content={"Add"}
                        />
                      ) : (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary "}
                          loading={loading}
                          disabled={lock}
                          title={"Update"}
                          content={"Update"}
                        />
                      )}
                    </div>
                  </Form>
                );
              }}
            </Formik>
          </div>
        </div>
      </div>
    </>
  );
};
export default CreatePrintCredit;
