import * as API from "./api";
import * as actions from "./actions";
import { errorFunction, successFunction } from "../../Component/Alert";
//get  Bank
export const getPrint = (postsPerPage) => async (dispatch) => {
  try {
    dispatch(actions.loadingAction());
    const { data } = await API.getPrint(postsPerPage);
    dispatch(actions.getPrintSuccessAction(data));
  } catch (error) {
    dispatch(actions.getPrintFailAction(error));
    errorFunction(error);
  }
};
