export const createPrintCredit =
  (
    {
      first_name,
      first_name_nepali,
      middle_name,
      middle_name_nepali,
      last_name,
      last_name_nepali,
      address,
      address_nepali,
      citizenship_no,
      citizenship_no_nepali,
      mobile_no,
      mobile_no_nepali,
      district,
      branch,
      qr,
      owner_photo,
    },
    history
  ) =>
  async (dispatch) => {
    try {
      const body = new FormData();
      body.append("first_name", first_name);
      body.append("first_name_nepali", first_name_nepali);
      body.append("middle_name", middle_name);
      body.append("middle_name_nepali", middle_name_nepali);
      body.append("last_name", last_name);
      body.append("last_name_nepali", last_name_nepali);
      body.append("address", address);
      body.append("address_nepali", address_nepali);
      body.append("mobile_no", mobile_no);
      body.append("mobile_no_nepali", mobile_no_nepali);
      body.append("citizenship_no", citizenship_no);
      body.append("citizenship_no_nepali", citizenship_no_nepali);
      body.append("district", JSON.stringify(district));
      body.append("branch", JSON.stringify(branch));
      if (qr) {
        body.append("qr", qr);
      }
      if (owner_photo) {
        body.append("owner_photo", owner_photo);
      }

      dispatch(actions.loadingAction());
      const { data } = await API.createPrint(body);
      dispatch(actions.createPrintSuccessAction(data));
      successFunction("Record Added Successfully.");
      history.push("/print-credit");
    } catch (error) {
      dispatch(actions.createPrintFailAction(error));
      errorFunction("Failed to create record.");
    }
  };

//update
export const updatePrintCredit = (id, history, values) => async (dispatch) => {
  try {
    dispatch(actions.loadingUpdatedAction());
    const {
      first_name,
      first_name_nepali,
      middle_name,
      middle_name_nepali,
      last_name,
      last_name_nepali,
      address,
      address_nepali,
      citizenship_no,
      citizenship_no_nepali,
      mobile_no,
      mobile_no_nepali,
      district,
      branch,
      qr,
      owner_photo,
      remarks,
    } = values;
    const body = new FormData();
    body.append("first_name", first_name);
    body.append("first_name_nepali", first_name_nepali);
    body.append("middle_name", middle_name);
    body.append("middle_name_nepali", middle_name_nepali);
    body.append("last_name", last_name);
    body.append("last_name_nepali", last_name_nepali);
    body.append("address", address);
    body.append("address_nepali", address_nepali);
    body.append("mobile_no", mobile_no);
    body.append("mobile_no_nepali", mobile_no_nepali);
    body.append("citizenship_no", citizenship_no);
    body.append("citizenship_no_nepali", citizenship_no_nepali);
    body.append("district", JSON.stringify(district));
    body.append("branch", JSON.stringify(branch));
    if (qr) {
      body.append("qr", qr);
    }
    if (owner_photo) {
      body.append("owner_photo", owner_photo);
    }
    const { data } = await API.updatePrint(id, body);
    dispatch(actions.updatedPrintSuccessAction(data));
    successFunction("Record Updated Successfully ");
    history.push("/print-credit");
  } catch (error) {
    dispatch(actions.updatedPrintFailAction(error));
    errorFunction("Failed to Update Record");
  }
};
